<template>
    <div>
        <!-- 注册界面 -->
        <div class="login">
           <div class="background">
                <svg>
                <defs>
                    <filter id="background" x="0" y="0">
                        <feImage xlink:href="https://ac.gtimg.com/media/images/ac_logo.png"  x="0" y="0" preserveAspectRatio="xMidYMid slice"></feImage>
                         <feGaussianBlur stdDeviation="30"></feGaussianBlur>
                    </filter>
                </defs>
                <rect x="0" y="0" filter="url(#background)">
                </rect>
            </svg>
           </div>
            <div class="content">
                <h1>炫火漫</h1>
               <div class="data">
                   <div class="account">
                        账号：<input type="text" placeholder="请输入账号" v-model="account">
                        <span>{{$store.state.user. register_input_error}}</span>
                   </div>
                    <div class="password">
                        用户名：<input type="text" placeholder="请输入用户名" v-model="name" >
                   </div>
                   <div class="password">
                        密码：<input type="password" placeholder="请输入密码" v-model="password" >
                   </div>
                    <div class="password">
                        确认密码：<input type="password" placeholder="请确认密码" v-model="again_password" >
                   </div>
               </div>
               <van-button color="#ff5127" @click="$store.commit('user/register',{account,name,password,again_password})">注册</van-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            account:"aaaa",
            name:"aaaa",
            password:"aaaa",
            again_password:"aaaa"
        }
    },
    watch:{
      "$store.state.user.isregister":function(newvalue){
            if(newvalue==true){
               
                this.$router.push({path:"/login"});
               
            }
        }
    },
    created(){
        this.$store.commit('user/init_register');
    }
}
</script>
